.custom-select {
  position: relative;
}

.custom-select__header {
  position: relative;
  min-height: 43px;
  padding: 3px 53px 0 19px;
  border: 1px solid $gray-color;
  border-radius: 3px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-wrap: wrap;
}

.custom-select__selected {
  position: relative;
  margin-bottom: 3px;
  margin-right: 10px;
  padding: 0 24px 0 5px;
  min-height: 28px;
  font-size: 16px;
  color: $black-color;
  background-color: $gray-standard-color;
  border: none;
  border-radius: 3px;

  &::before,
  &::after {
    content: " ";
    position: absolute;
    top: 6px;
    right: 11px;
    width: 1px;
    height: 12px;
    background-color: $black-color;
  }

  &::before {
    transform: rotate(45deg);
  }

  &::after {
    transform: rotate(-45deg);
  }
}

.custom-select__header .custom-select__selected:last-child {
  // margin-bottom: 0;
}

.custom-select__toggle-icon {
  position: absolute;
  top: -1px;
  bottom: -1px;
  right: -1px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 33px;
  background-color: transparent;
  border: 1px solid $gray-color;
  border-top-right-radius: 3px;
  border-bottom-right-radius: 3px;
}

.custom-select__body {
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  display: none;
  padding: 0;
  background-color: $white-color;
  border: 1px solid $gray-color;
  border-top: none;
  border-radius: 0 0 3px 3px;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16);
  z-index: 2;
}

.custom-select__body .search {
  margin-right: 0;
  padding: 15px 10px;
  max-width: 100%;
}

.custom-select__list {
  margin: 0 0 15px 0;
  padding-left: 0;
  list-style: none;
  max-height: 150px;
}

.custom-select__option {
  display: block;
  width: 100%;
  padding: 10px;
  text-align: left;
  background-color: transparent;
  border: none;
  outline: none;
  cursor: pointer;
  &:hover {
    background-color: $gray-standard-color;
  }
}

.custom-select__selected:focus {
  opacity: 0.3;
  outline: none;
}

.custom-select__header:focus {
  outline: none;
  border-color: $yellow-color;
}

.custom-select__header:focus .custom-select__toggle-icon {
  border-color: $yellow-color;
}

.custom-select__body .input--search {
  &:focus {
    border-color: $yellow-color;
    box-shadow: none;
  }
}

@media (min-width: $laptop) {
  .custom-select__selected {
    &:hover {
      opacity: 0.6;
    }

    &:focus {
      opacity: 0.3;
    }
  }

  .custom-select__header:hover .custom-select__toggle-icon {
    border-color: $black-color;
  }

  .custom-select__header:focus .custom-select__toggle-icon {
    border-color: $yellow-color;
  }

  .custom-select__header {
    &:hover {
      border-color: $black-color;
    }

    &:focus {
      border-color: $yellow-color;
    }
  }

  .custom-select__body .input--search {
    &:hover {
      border-color: $black-color;
    }

    &:focus {
      border-color: $yellow-color;
      box-shadow: none;
    }
  }

}
